function About()
{
	const[message, setMessage] = useState(null);

	useEffect(() => {
		fetch("/about")
		.then(response => response.json())
		.then(data => setMessage(data.time))
		},[])

	return (
		<div>
		<p>Hello from About: {message}</p>
		<Link to="/contact">Go to contact page</Link>
		</div>

	);
}

 
 export default About

